<template>
  <div id="loginlog">
    <div class="tits">
      <span class="title">登录记录</span>
      <div class="content">&nbsp;</div>
      <DatePicker type="daterange" :value="time" split-panels placeholder="指定登录时间" style="width: 240px"
        @on-change="datechange($event)"></DatePicker>
    </div>
    <div class="store_bottom">
      <table>
        <thead>
          <tr>
            <th>登录时间</th>
            <th>IP</th>
            <th>登录区域</th>
            <th>登录设备</th>
          </tr>
        </thead>
        <tbody>
          <td v-if="list.length  == 0" colspan="11" align="center">
            <div class="none_now">
              <img src="/static/icon/7.png">
              <div>暂无相关数据</div>
            </div>
          </td>
          <tr v-else v-for="(item,index) in list" :key="item.Id">
            <td>{{item.time}}</td>
            <td>{{item.ip}}</td>
            <td>{{item.address}}</td>
            <td>{{item.user_agent}}</td>
          </tr>
        </tbody>
      </table>
      <Page :total="page.total" :page-size="15" show-total :current="page.current" class="fenye" v-if="list.length >= 1"
        @on-change="ajaxlist($event,time)" />
    </div>
    <Spin fix v-if="spin"></Spin>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        spin: true,
        list: [],
        time: '',
        page: {
          current: 1,
          total: ''
        }
      }
    },
    mounted() {
      this.time = [this.getDay(0),this.getDay(1)];
      this.ajaxlist(1,this.time);
    },
    methods: {
      getDay(num) {
          var today = new Date();
          var nowTime = today.getTime();
          var ms = 24*3600*1000*num;
          today.setTime(parseInt(nowTime + ms));
          var oYear = today.getFullYear();
          var oMoth = (today.getMonth() + 1).toString();
          if (oMoth.length <= 1) oMoth = '0' + oMoth;
          var oDay = today.getDate().toString();
          if (oDay.length <= 1) oDay = '0' + oDay;
          return oYear+'-'+oMoth+'-'+oDay;
      },
      ajaxlist(page, time) {
        this.spin = true;
        this.$http.post('api/user/loginlog',{page:page,time:time}, res => {
              this.spin = false;
             if (res.status == 200) {
                 if(res.data.code == 200){
                  this.list = res.data.data.list;
                  this.page.current = res.data.data.current_page;
                  this.page.total = res.data.data.total;
                 }else if(res.data.code == 201){
                   this.$Message.warning(res.data.msg);
                 }
             } else {
                console.log(res.message);
             }
        });
      },
      datechange(n) {
        this.time = n;
        this.ajaxlist(1,this.time);
      }
    }
  }
</script>



